<style lang="less" scoped>
.realtime{
  padding-top: 20px;
  .map {
  width: 100%;
  height: 400px;
  .cascader{
  width: 300px;
}
}
}

</style>
<template>
  <div class="realtime">
    <el-tabs type="card">
      <el-tab-pane>
        <span slot="label">区域监控
          <i class="el-icon-date"></i>
        </span>
      </el-tab-pane>
      <el-tab-pane>
        <span slot="label">停车场监控
          <i class="el-icon-date"></i>
        </span>
      </el-tab-pane>
      <el-tab-pane>
        <span slot="label">违章监控
          <i class="el-icon-date"></i>
        </span>
      </el-tab-pane>
      <el-tab-pane>
        <span slot="label">停车诱导
          <i class="el-icon-date"></i>
        </span>
      </el-tab-pane>
    </el-tabs>
    <baidu-map class="map" :center="center" :zoom="zoom" @ready="handler">
      <bm-city-list class="cascader" anchor="BMAP_ANCHOR_TOP_RIGHT" offset="40"></bm-city-list>
    </baidu-map>
  </div>
</template>
<script>
  export default {
    name: 'realtime',
    data () {
      return {
        center: {lng: 0, lat: 0},
        zoom: 3,
      }
    },
    methods: {
      handler ({BMap, map}) {
        console.log(BMap, map)
        this.center.lng = 116.404
        this.center.lat = 39.915
        this.zoom = 15
      }
    }
  }
</script>


